module.exports = "uniform float time;\nuniform float fogDensity;\nuniform vec3 fogColor;\nuniform sampler2D texture1;\nuniform sampler2D texture2;\nvarying vec2 vUv;\n#define M_PI 3.14159265358979323846\nfloat rand(vec2 co) {\n\treturn fract(sin(dot(co.xy, vec2(12.9898, 78.233))) * 43758.5453);\n}\nfloat rand(vec2 co, float l) {\n\treturn rand(vec2(rand(co), l));\n}\nfloat rand(vec2 co, float l, float t) {\n\treturn rand(vec2(rand(co, l), t));\n}\nfloat perlin(vec2 p, float dim, float time) {\n\tvec2 pos = floor(p * dim);\n\tvec2 posx = pos + vec2(1.0, 0.0);\n\tvec2 posy = pos + vec2(0.0, 1.0);\n\tvec2 posxy = pos + vec2(1.0);\n\tfloat c = rand(pos, dim, time);\n\tfloat cx = rand(posx, dim, time);\n\tfloat cy = rand(posy, dim, time);\n\tfloat cxy = rand(posxy, dim, time);\n\tvec2 d = fract(p * dim);\n\td = (-0.5 * cos(d * M_PI)) + 0.5;\n\tfloat ccx = mix(c, cx, d.x);\n\tfloat cycxy = mix(cy, cxy, d.x);\n\tfloat center = mix(ccx, cycxy, d.y);\n\treturn (center * 2.0) - 1.0;\n}\nfloat perlin(vec2 p, float dim) {\n\treturn perlin(p, dim, 0.0);\n}\nvoid main() {\n\tvec2 position = -1.0 + (2.0 * vUv);\n\tvec4 noise = texture2D(texture1, vUv);\n\tfloat noise2 = perlin(vUv, 1.0, time * 0.1);\n\tvec2 T1 = vUv + ((vec2(.15, -.8) * time) * 0.01);\n\tvec2 T2 = vUv + ((vec2(-.15, .3) * time) * 0.01);\n\tfloat p = texture2D(texture1, T1 * 2.0).a;\n\tvec4 color = texture2D(texture2, T2 * 2.0);\n\tvec4 temp = (color * (vec4(p, p, p, p) * 2.0)) + ((color * color) - 0.1);\n\tgl_FragColor = temp;\n\tfloat depth = gl_FragCoord.z / gl_FragCoord.w;\n\tconst float LOG2 = 1.442695;\n\tfloat fogFactor = exp2((((-fogDensity * fogDensity) * depth) * depth) * LOG2);\n\tfogFactor = 1.0 - clamp(fogFactor, 0.0, 1.0);\n\tgl_FragColor = mix(gl_FragColor, vec4(fogColor, gl_FragColor.w), fogFactor);\n}\n"